main {
    .home {
        .promo-area__wrap {
            .layout__right--split,
            .layout__left--split {
                @include media('<small', '>tablet') {
                    @include default-margin();
                }
                @include media('>=small', '<desktop') {
                    .promo-area__image {
                        grid-column: 2/15;
                    }
                    .promo-area__content {
                        grid-column: 14/-2;
                    }
                }
            }
        }
    }
}
